import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import { isMobile } from 'react-device-detect';

function Hero() {
	const navigate = useNavigate();
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};

	const handleBookAppointmentClick = () => {
		navigate('/appointment');
	};

	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (
		<>
			

			<div className="section-container" style={{ height: isMobile ? '60vh' : '100vh', padding: isMobile ? '120px 0 0 0' : '', alignContent: 'center' }}>
				<div className="hero-section" style={{ alignContent: 'center', alignItems: 'center', justifyItems: 'center', display: 'flex', backgroundColor: '#003C71' }}>

					<div className="text-section" style={{ padding: isMobile ? '0 20px' : '0 80px', display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
						<p className="text-headline" style={{ color: 'white' }}>
							{`Work with us today`}
						</p>
						<h2 className="text-title" style={{ fontWeight: isMobile ? 'bold' : 'normal', fontSize: isMobile ? '' : '50px', color: 'white' }}>
							{`Impact through simple solutions`}
						</h2>
						<p className="text-descritpion" style={{ color: 'white' }}>
							{`We listen, understand, and deliver simple effective solutions. With clear requirements and strong collaboration, no solution is too complex. Let our technical expertise transform your business processes.`}
						</p>
					</div>

					<div style={{ display: isMobile ? 'none' : 'flex', flexDirection: 'column', alignItems: 'center' }}>
						<div className="ad-section-container" />
					</div>

				</div>
			</div>
			<div className="hero-section" style={{ display: isMobile ? 'none' : 'flex', flexDirection: 'column' }}>
				<div className="text-title" style={{ fontSize: '30px', fontWeight: 'normal' }}>
					{`We turn the digital dreams of our partners into reality.`}
				</div>
				<button className="text-appointment-btn" type="button" onClick={handleBookAppointmentClick} style={{}}>
					<FontAwesomeIcon icon={faCalendarCheck} /> {`Contact Us To Find How`}
				</button>
			</div>

			<div className="hero-section" style={{ display: isMobile ? 'none' : '', height: '60vh', backgroundColor: '#FFDEB3' }}>
				<div className="text-section" style={{ padding: '0px 50px 0 50px' }}>
					<h2 className="text-title" style={{ fontWeight: isMobile ? 'bold' : 'normal', fontSize: isMobile ? '' : '50px' }}>{`Committed to produce exceptional work`}</h2>
					<p className="text-descritpion">{`We are committed to producing exceptional software for our partners and work closely with our users throughout development to ensure that we are aligned with the end-goal.`}</p>
				</div>
				<div className="text-section" style={{ padding: '0px 50px 0px 50px' }}>
				</div>
			</div>

			{/* <div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
				<FontAwesomeIcon icon={faAngleUp} />
			</div> */}
		</>
	);
}
export default Hero;